.login {
    .submit-button,
    .forgot-password,
    .support-text {
        font-family: 'Century Gothic', 'Open Sans', sans-serif;
    }

    .support-text {
        width: 80%;
        margin: 15px auto 0;
        text-align: center;
        font-size: 14px;
    }
}

.login-footer {
    text-align: center;
    width: 100%;
    height: fit-content;
    padding: 20px;
    
    a {
        text-decoration: underline;
    }
}